import { CompareResult } from './types.js';
import { clean, compareVersion, validateWithError } from './utils.js';

/**
 * @description
 * Determines if version A is less than or equal to version B.
 *
 * @param verA - The first version to compare.
 * @param verB - The second version to compare.
 * @returns True if version A is less than or equal to version B, false otherwise.
 *
 * @docsCategory preference/utils/semver
 * @docsPage semver-lte
 * @codeFilter lte
 */
export const lte = (verA: string, verB: string): boolean => {
  if (validateWithError(verA, verB)) {
    const result = compareVersion(clean(verA), clean(verB));
    return result === CompareResult.LessThan || result === CompareResult.Equal;
  }
  return false;
};
